














export default{
	data(){
		return{
			changenickname:'',
			// nickname:uni.getStorageSync('login_key').nickname,
		};
	},
	onLoad() {
	},
	methods:{
		rename:function(changenickname){
			var _this = this;
			uni.request({
			url: 'http://localhost:8080/api/user/nickname?id='+uni.getStorageSync('login_key').userId,
			method:'put',
			data:changenickname,
			header: { 'content-type':'application/json' },
				success: res => {
					uni.navigateBack();
					}
			});
		}	
		}
}
